@charset "UTF-8";
.container-special {
  width: 960px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.modal__wrap {
  width: 100%;
  position: fixed;
  z-index: 1000;
  bottom: 0;
  left: 0;
}
.modal__container {
  width: 960px;
  margin-left: auto;
  margin-right: auto;
}

.modal-formula {
  box-shadow: 0 0 10px rgba(34, 34, 34, 0.609);
}

.modal-formula__accordion-button {
  height: 50px;
  background-color: #1e1e1e;
  padding: 10px;
  color: #fff;
  width: 100%;
  border: none;
  font-size: 22px;
  -moz-text-align-last: left;
  text-align-last: left;
  outline: none;
  transition: 0.4s;
}
.modal-formula__accordion-button::after {
  content: "+";
  color: #fff;
  font-weight: bold;
  float: right;
  margin-left: 5px;
}

.form-arrow {
  float: right;
  height: 30px;
  width: 30px;
  cursor: pointer;
}

.convert-form {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  max-height: 0;
  background-color: #e4e4e4;
  transition: max-height 0.2s ease-out;
}
.convert-form p {
  padding: 20px;
  font-size: 1.2rem;
}

.convert-formula_visible {
  max-height: 107px;
}

.accordion-button_active::after {
  content: "âˆ’";
  color: #fff;
}

.convert-form__input {
  font-size: 1.2rem;
}
.convert-form__input::-webkit-inner-spin-button, .convert-form__input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

.convert-form__button {
  font-size: 1.2rem;
  text-transform: uppercase;
  padding: 5px 10px;
  border-radius: 20px;
  border: none;
  background-color: #fff;
  transition: 0.2s ease-in;
}
.convert-form__button:hover {
  box-shadow: 0 0 10px rgba(34, 34, 34, 0.609);
}